import React from 'react';
import Table from './Table.js'
import Graph from './Graph.js'

export default class Field extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            activities: [],
            parameters: [],
            result: '',
            isEdit: false,
            editable: '',
            loading: true
        };
    }

    async componentDidMount() {
        const requestOptions = {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' }
        };

        try {
            const response = await fetch('https://mainapi-react.appspot.com/main/all', requestOptions);
            const data = await response.json();

            if (response.ok) 
            {
                const values = []

                data.forEach(activity => {
                    values.push([activity.userID, activity.daysByActivity])
                });

                values.sort(function(x, y) {
                        if (x[1]> y[1]) {
                          return 1;
                        }
                        if (x[1] < y[1]) {
                          return -1;
                        }
                        return 0;
                      });

                this.setState({activities: data, parameters: values})
            }
            else {
                try {
                    alert(JSON.stringify(data.errors))
                }
                catch {
                    alert(data)
                }
            }
        }
        catch (ex) {
            alert("Отсутствует соединение с сервером")
        }

        this.setState({loading: false})
    }

    onChangeMode = userID => { 

        if (this.state.isEdit && userID !== this.state.editable) return;

        this.setState({
            editable: this.state.isEdit ? '' : userID,
            isEdit: this.state.isEdit ? false : true
        }) 
    } 

    onUpdate = async (userID, dateRegistration, dateLastActivity) => {
        if (dateRegistration.length < 1 || dateLastActivity.length < 1) {
            alert("Некорректная дата")
            return;
        }

        if (new Date(dateRegistration).getTime() > new Date(dateLastActivity).getTime()){
            alert("Дата регистрации должна быть раньше даты последней активности")
            return;
        }

        if (new Date().getTime() < new Date(dateRegistration).getTime || new Date().getTime() < new Date(dateLastActivity).getTime()) {
            alert("Дата не может быть больше текущей")
            return;
        }

        const requestOptions = {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                userID: userID,
                dateRegistration: dateRegistration,
                dateLastActivity: dateLastActivity
            })
        };

        try {

            const response = await fetch('https://mainapi-react.appspot.com/main/update', requestOptions)

            if (response.ok) 
            {
                const data = await response.json()
                const newActivities = this.state.activities;
                newActivities.forEach(function (activity) {
                    if (activity.userID === userID) {
                        activity.dateRegistration = data.dateRegistration
                        activity.dateLastActivity = data.dateLastActivity
                        activity.daysByActivity = data.daysByActivity
                    }    
                  });

                this.setState({ activities: newActivities })
            }
            else {
                console.error(response.body) 
                alert("Ошибка " + response.status)
            }
        } 
        catch (ex) {
            console.error(ex)
            alert(ex)
        }

        this.onChangeMode(userID)
    }

    onCalculate = async () => {
        if (this.state.activities.length < 1) return;

        const requestOptions = {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        };

        try {
            const response = await fetch('https://mainapi-react.appspot.com/main/retention', requestOptions);
            const data = await response.json(); 

            if (response.ok) 
            {
                const values = []

                this.state.activities.forEach(activity => {
                    values.push([activity.userID, activity.daysByActivity])
                });

                values.sort(function(x, y) {
                        if (x[1] > y[1]) {
                          return 1;
                        }
                        if (x[1] < y[1]) {
                          return -1;
                        }
                        return 0;
                      });

                this.setState({result: `Rolling Retention 7 day: ${data.result} %`, parameters: values})
            }
            else {
                console.log(data)
                alert(`Ошибка запроса: ${response.status}`)
            }
        }
        catch (ex) {
            console.log(ex)
            alert(ex)
        }

        document.getElementById("graph").className = "graph"
    }

    render() {
        return (
            <div className="field">

                <div className="in">
                    <h1 className="field-title">Activities</h1>

                    {
                        this.state.loading ? <div className="lds-dual-ring"></div> :
                        this.state.activities.length > 0 &&
                        <Table 
                            editable={this.state.editable}
                            data={this.state.activities} 
                            isEdit={this.state.isEdit}
                            onChangeMode={this.onChangeMode}  
                            onRemove={this.onRemoveUser}
                            onUpdate={this.onUpdate}
                        />
                    }
                    
                    {
                        this.state.activities.length > 0 &&
                        <div className="calculate-place">
                            <h3 className="retention">{this.state.result}</h3>
                            <button type="submit" className="calculate" onClick={this.onCalculate}>Calculate</button>
                        </div>
                    }
                    
                    {
                        this.state.parameters.length > 0 &&
                        <div id="graph" className="graph-none">
                            <Graph data={this.state.parameters} />
                        </div>
                    }

                </div>  
            </div>
        )
    }
}     