import React from 'react'

export default class Table extends React.Component {
    render() {
        return (
                <table className="table">
                    <thead>
                        <tr>
                            <th>User ID</th>
                            <th>Registration</th>
                            <th>Last Activity</th>
                            <th>Active</th>
                        </tr>
                    </thead>

                    <tbody>{
                        this.props.data.map(activity => {
                            return (
                                this.props.isEdit && this.props.editable === activity.userID ? 
                                <RowEdit 
                                    activity={activity} 
                                    onUpdate={this.props.onUpdate} 
                                    onChangeMode={this.props.onChangeMode}
                                    key={activity.userID} /> 
                                    :
                                <Row 
                                    activity={activity} 
                                    onRemove={this.props.onRemove} 
                                    onChangeMode={this.props.onChangeMode}
                                    key={activity.userID} />
                        )})
                    }
                    </tbody>
                </table>
        )
    }
}

class Row extends React.Component {
    render() {
        return (
            <tr>
                <td>{this.props.activity.userID}</td>
                <td>{this.props.activity.dateRegistration}</td>
                <td>{this.props.activity.dateLastActivity}</td>
                <td>
                    <button className="edit-btn" onClick={() => this.props.onChangeMode(this.props.activity.userID)}>EditMode</button>
                </td>
            </tr>
        )
    }
}

class RowEdit extends React.Component {
    constructor(props){
        super(props)

        this.state = {
            dateRegistration: this.props.activity.dateRegistration.split(".").reverse().join("-"), 
            dateLastActivity: this.props.activity.dateLastActivity.split(".").reverse().join("-")
        }

        this.onChangeNewReg = this.onChangeNewReg.bind(this)
        this.onChangeNewLast = this.onChangeNewLast.bind(this)  
    }

    onChangeNewReg = e => this.setState({dateRegistration: e.target.value})
    onChangeNewLast = e => this.setState({dateLastActivity: e.target.value})

    render() {
        return (
            <tr id={this.props.activity.userID} key={this.props.activity.userID}>
                <td>{this.props.activity.userID}</td>
                <td><input className="change-dt" type="date" value={this.state.dateRegistration} onChange={this.onChangeNewReg} /></td>
                <td><input className="change-dt" type="date" value={this.state.dateLastActivity} onChange={this.onChangeNewLast} /></td>
                <td>
                    <button className="save-btn" onClick={() => this.props.onUpdate(this.props.activity.userID, this.state.dateRegistration, this.state.dateLastActivity)}>Submit</button>
                    <button className="remove-btn" onClick={() => this.props.onChangeMode(this.props.activity.userID)}>Cancel</button>
                </td>
            </tr>
        )
    }
}