import React from 'react'
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'
import bellCurve from 'highcharts/modules/histogram-bellcurve';
bellCurve(Highcharts);

const GraphData = {
    chart: {
        style: {
            fontFamily: 'Ubuntu',
            color: "#3C5AA8"
        },
        borderRadius: 5,
    },
    title: {
        text: '<p style="color: #3C5AA8; opacity: 0.4">Users Lifetimes Histogram<p>'
    },
    xAxis: [{
        title: { text: 'Users ID' }, // axis x0
        visible: false,
        opposite: true
    }, {
        title: { text: 'Lifetime days intervals' }, // axis x1 дни жизни пользователей группы (группировка по количеству дней)
        startOnTick: true,
        gridLineWidth: 2,
        endOnTick: true,
        minRange: 20,
        minorTicks: true,
        tickInterval: 0.2,
        tickPixelInterval: 150,
        style: {
            fontFamily: 'Ubuntu',
            color: "#3C5AA8"
        },
    }],

    yAxis: [{
        title: { text: '' }, // axis y0 время жизни пользоватя
        opposite: true
    }, {
        title: { text: 'Users number' }, // axis y1 количество пользователей с x1 временем жизни
        style: {
            fontFamily: 'Ubuntu',
            color: "#3C5AA8"
        },
    }],

    tooltip: {
        formatter () {
            let text = ''
             
            if (this.point.series.name === 'Histogram') {
                text = `<p style="color: #4A9DFF">Lifetime days interval: ${this.point.x} - ${this.point.x2}</p>`
                text += `<p style="color: #4A9DFF">Users number: ${this.point.y}</p>`
            }
            else {
                text = `<p style="color: black">User ID: ${this.key}</p>`
                text += `<p style="color: black">User lifetime:${this.point.y}</p>`
            }

            return text
        },
        useHTML: true
    },

    series: [{
        name: 'Histogram',
        type: 'histogram',
        xAxis: 1,
        yAxis: 1,
        baseSeries: 's1',
        zIndex: -1
    }, {
        name: 'Days of user lifetime',
        type: 'scatter',
        data: [0],
        id: 's1',
        marker: {
            radius: 3
        },
        visible: false
    }]
};

export default class Graph extends React.Component {

    constructor(props) {
        super(props)
        this.state = {data: this.props.data}
    }

    componentWillReceiveProps(nextProps) {
        GraphData.series[1].data = nextProps.data
        this.setState({data: nextProps.data})
      }

    render() {
        return (
            <HighchartsReact 
                highcharts={Highcharts}
                options={GraphData}
                oneToOne={true}
                allowChartUpdate={true}
                updateArgs={[true, true, true]}
            />
        )
    }
} 